.close-overlay {
  position: fixed;
  background: rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 4;
  transition: all 0.2s ease;
}
.close-overlay.active {
  opacity: 1;
  visibility: visible;
}

.menu-btn {
  color: #fff;
  cursor: pointer;
  position: relative;
  display: block;
  margin-left: 0.5rem;
  outline: none;
  width: 30px;
  height: 22px;
  border: none;
  background: transparent;
  border-top: 4px solid;
  border-bottom: 4px solid;
  font-size: 0;
  transition: all 0.1s ease;
}
.menu-btn:before, .menu-btn:after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  background: currentColor;
  transition: transform 0.1s ease;
}
.menu-btn.active {
  border-color: transparent;
}
.menu-btn.active:before {
  transform: rotate(45deg);
}
.menu-btn.active:after {
  transform: rotate(-45deg);
}

.snazzymenu .controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.25rem 1rem;
}
.snazzymenu .controls .logo {
  margin-right: 1rem;
}
.snazzymenu .controls .logo a {
  padding: 0;
}
.snazzymenu .controls .logo img {
  max-width: 100%;
  height: auto;
}
.snazzymenu .controls .cta {
  display: flex;
  align-items: center;
}
.snazzymenu .controls a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 0.5rem;
}
.snazzymenu .controls a span {
  margin-left: 0.25rem;
}
.snazzymenu .controls svg {
  fill: currentColor;
}